html{
  background: #373B44;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #4286f4, #373B44); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  font-family: 'Rye', cursive;
}


*{
    color:#FFA16A;
}


button{
    position:absolute;
    top:75%;
    left:80%;
    -webkit-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);
}

button{
    border:none;
    padding:1em 2em;
    background: #c31432;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #240b36, #c31432); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    color:#fff;
    text-transform: uppercase;
    cursor: pointer;
}

 
.quote-wrapper_cont{
    display: -webkit-flex;
    display: flex;
    position:absolute;
    top:50%;
    left:50%;
    -webkit-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);
    border: 1px dotted #FFA16A;
    width:80%;
    height:300px;
    padding: 10px;
    text-transform: capitalize;
}

.quote-wrapper_cont h1{
    text-align: center;
    margin: 0;
    padding: 2em;
}

p{
    font-size: 2em;
    font-weight: 900;
    float:right;
    bottom:10px;
    position:absolute;
    left:75%;
    text-align: center;
}


.loader-icon{
    position:absolute;
    top:50%;
    left:50%;
    -webkit-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);
}
